import React, { useEffect } from "react";
import { Analyzer } from "fuzzy-searcher";
import { FormField, Heading, Page, PageContent, Paragraph, TextArea, TextInput } from "grommet";


export const Examples = () => {
  const anlyzer = new Analyzer();
  const [value, setValue] = React.useState('');
  const [key, setKey] = React.useState('');
  const [matches, setMatches] = React.useState<string[]>([]);
  useEffect(() => {
    anlyzer.load(value.split(","));
    setMatches(anlyzer.search(key, 1));
  }, [value, key]);

  return (
    <Page>
      <Heading alignSelf='center' level={2}>Examples</Heading>
      <PageContent>
        <Paragraph fill>
          <FormField label="Words">
            <TextArea
              placeholder="type here comma separated values"
              value={value}
              onChange={event => setValue(event.target.value)}
            />
          </FormField>

          <FormField label="Key">
            <TextInput
              placeholder="type here key to search"
              value={key}
              onChange={event => setKey(event.target.value)}
            />
          </FormField>
          <Heading alignSelf='center' level={3}>Results</Heading>
          {matches.join(',')}
        </Paragraph>
      </PageContent>
    </Page>
  );
}